<template>
  <div class="detail">
    <div class="detailContent">
      <div class="detailTitle">
        <span class="back" @click="goBack">成果审核</span>
        <i class="el-icon-arrow-right"></i> 
        <span>成果详情</span>
      </div>

      <div class="article">
        <div class="articleContent">
          <div class="faceImgDiv">
            <img
             :src="blog.coverPictures.pictureUrl" 
             v-if="blog.coverPictures" 
             class="faceImg" 
             :style="imgSize"
            >
            <img :src="blog.coverPictures.pictureUrl" alt="" style="display:none" v-if="blog.coverPictures" @load="GetImageSize(blog.coverPictures.pictureUrl)">
          </div>
          <h1 class="blogTitle">{{blog.blogTitle}}</h1>
          <div class="blogName">
            <p>
              <img :src="blog.userHeadImgRelative || avatar" class="avatar">
              <span>{{blog.userNickname}}</span>
              <em>{{blog.createTime | getdate}}</em>
            </p>

            <div class="blogContent">
              <div class="blogVideo" v-if="blog.blogAttachments">
                <div v-if="blog.blogAttachments.length != 0">
                  <video 
                    :src="blog.blogAttachments[0].videoUrl" 
                    v-if="blog.blogAttachments[0].videoUrl.indexOf('http://') != -1"
                    controls="controls"
                    style="width:100%;" 
                  >
                </video>
                </div>
              </div>
              <div class="blogText" v-html="blog.blogContent" ref="blogText">
              </div>
            </div>

            <div class="type">
              {{blog.tagTypeName1}} / {{blog.tagTypeName2}}
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<style lang="less" scoped>
  .detailContent {
    margin: 60px 0 60px 260px;
    .detailTitle{
      color: #808080;
      height: 58px;
      font-size: 14px;
      line-height: 58px;
      text-align: left;
      padding-left: 50px;
      border-bottom: 1px solid #eee;
      .back{
        color: black;
        cursor: pointer;
      }

    }
    .article{
      padding: 20px 50px;
      .articleContent{
        border: 1px solid #ccc;
        padding: 20px 200px;
        overflow: hidden;
        .blogTitle{
          font-size: 20px;
        }
        .faceImgDiv{
          img{
            object-fit: cover;
            margin-bottom:50px;
          }
        }
        .blogName{
          text-align: left;
          .avatar{
            width: 30px;
            height: 30px;
            border-radius: 100%;
            vertical-align: middle;
            margin-right: 5px;
          }
          .blogText{
            margin: 40px 0;
            font-size: 16px;
          }
          em{
            font-style: normal;
            color: #ccc;
            margin-left: 20px;
          }
          .type{
            text-align: right;
            color: #ccc
          }
        }
      }
    }
  }
</style>
<script>
import avatar from "../../assets/images/avatar.jpg"
export default {
  data () {
    return {
      blog : {},
      avatar: avatar,
      isHeight: false, // 控制图片的大小
      imgSize:{
        width:'',
        height:''
      }
    }
  },
  created() {
    this.getDetails()
  },
  mounted() {

  },
  methods:{
    goBack() {
       this.$router.back(-1)
    },
    getDetails() {
      let blogId = this.$route.query.blogId;
      let params = {
        blogId: blogId
      };
      this.$post(`${this.url}/moments/blog/getBlogInfo`,
        params
      )
      .then((response) => {
        if(response.code == 200){
          this.blog = response.data;
          console.log(this.blog)
        }else{
          this.$message.error('请求失败，请稍后重试');
        }
      })
    },
    GetImageSize(oImageSrc) {
      let originImage = new Image();
      if (originImage.src != oImageSrc){
        originImage.src = oImageSrc;
      } 
      console.log(this.$refs.blogText.offsetWidth)
      let textWdith = this.$refs.blogText.offsetWidth;
      if(originImage.width>textWdith){
          this.imgSize.width = textWdith + 'px';
          this.imgSize.height = textWdith*9/16 + 'px'
      }else{
          this.imgSize.width = originImage.width + 'px';
          this.imgSize.height = originImage.width*9/16 + 'px'
      }
    },
  }
}
</script>


